<template>
    <div class="content-table">
        <el-card class="box-card">
            <div slot="header" class="clearfix">
                <span>{{ headerCard }}</span>
                <router-link to="/application/add">
                <el-button icon="el-icon-plus" style="float: right; padding: 3px 0" type="text">Add Application</el-button>
                </router-link>
            </div>
        <div class="center">
            <vs-table>
                <template #thead>
                <vs-tr>
                    <vs-th>Icon</vs-th>
                    <vs-th>Application</vs-th>
                    <vs-th>Key</vs-th>
                    <vs-th>Play Store</vs-th>
                    <vs-th>Action</vs-th>  
                </vs-tr>
                </template>
                <template #tbody>
                <vs-tr
                    :key="i"
                    v-for="(tr, i) in $vs.getPage(dataApplications, page, max)"
                    :data="tr" >
                    <vs-td>
                        <el-image
                        style="width: 100px; height: 100px"
                        :src="tr.url"
                        fit="cover"></el-image>
                    </vs-td>
                    <vs-td>
                    {{ tr.name_applications }}
                    </vs-td>
                    <vs-td>
                    {{ tr.key_applications }}
                    </vs-td>
                    <vs-td>
                      <el-button @click="playStore(tr.package_applications)" size="small" target="_blank" round>Play Store</el-button>
                    </vs-td>
                    <vs-td>
                        <el-dropdown>
                        <span class="el-dropdown-link">
                            Action<i class="el-icon-arrow-down el-icon--right"></i>
                        </span>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item @click.native="detailData(tr.id_applications)" icon="el-icon-check">Detail</el-dropdown-item>
                            <el-dropdown-item @click.native="editData(tr.id_applications)" icon="el-icon-edit">Edit</el-dropdown-item>
                            <el-dropdown-item @click.native="deleteData(tr.id_applications)" icon="el-icon-delete">Delete</el-dropdown-item>
                        </el-dropdown-menu>
                        </el-dropdown>
                    </vs-td>
                </vs-tr>
                </template>
                <template #footer>
                <vs-pagination v-model="page" :length="$vs.getLength(dataApplications, max)" />
                </template>
            </vs-table>
        </div>
        </el-card>
    </div>
</template>

<script>

import moduleApplication from '../../store/applications/moduleApplication.js'
import router from '@/router'

  export default {
    data() {
      return {
        page: 1,
        max: 5,
      }
    },
    computed : {
        dataApplications () {
          return this.$store.state.dataApplications.applications
        },
        headerCard () {
            return this.$route.name
        },
        role () {
          return this.$store.state.userDataActive.role
        }
    },
    methods : {
      detailData(idApplication) {
        const id = idApplication
        router.push(`/application/${id}/detail`)
      },
      editData (idApplication) {
        const id = idApplication
        router.push(`/application/${id}/edit`)
      },
      deleteData(dataID) {
        this.$alert('Are you sure you delete this all image', 'Delete!', {
          confirmButtonText: 'OK',
          callback: action => {
            this.dataDel(dataID)
            this.$message({
              type: 'info',
              message: `action: ${ action }`
            });
          }
        });
      },
      dataDel(dataID) {        
        this.$store.dispatch('dataApplications/deleteApplication', dataID)
      },
      playStore (packagePlayStore) {
        const pl = packagePlayStore
        window.open('https://play.google.com/store/apps/details?id='+pl, '_blank'); 
      }
    },
    created () {
      if (!moduleApplication.isRegistered) {
        this.$store.registerModule('dataApplications', moduleApplication)
        moduleApplication.isRegistered = true
      }
      const obj = {
        id : this.$store.state.userDataActive.id
      }
      this.$store.dispatch('dataApplications/showApplication', obj)
    }
  }
</script>

<style>
.content-table {
    margin: 0;
    padding-left: 25px;
    padding-right: 25px;
}
</style>